<template>
  <el-card shadow="hover" style="width:70%;border-radius: 4px;margin:5rem auto;" v-loading="loading" element-loading-text="所需大概一分钟，请耐心等待~">
    <el-row>
      <el-col :span="22" style="text-align:center;">
        <h1>极客飞兔AI人脸编辑</h1>
      </el-col>
    </el-row>
    <el-row :gutter="20">
        <el-col :span="11">
            <el-form ref="form" :model="form" label-position="left" label-width="100px">
              <el-form-item label="个人自拍">
                <template v-slot:label>
                  <span>个人自拍
                    <el-tooltip class="item" effect="dark" placement="right" content="尽量五官清晰，纯色背景">
                      <el-icon style="vertical-align: middle;">
                        <Warning />
                      </el-icon>
                    </el-tooltip>
                  </span>
                </template>
                <el-upload 
                  class="avatar-uploader" 
                  action="/upload" 
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess" 
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="imageUrl" :src="imageUrl" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon">
                    <el-icon>
                      <Plus />
                    </el-icon>
                  </i>
                </el-upload>
              </el-form-item>
              <el-form-item label="变换类型">
                <el-select v-model="form.style" placeholder="请选择" style="width:100%">
                  <el-option 
                    v-for="item, key in styles" 
                    :key="key" 
                    :label="item" 
                    :value="key"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="处理程度">
                <template v-slot:label>
                  <span>处理程度
                    <el-tooltip class="item" effect="dark" placement="right" content="数字越小处理程度越轻">
                      <el-icon style="vertical-align: middle;">
                        <Warning />
                      </el-icon>
                    </el-tooltip>
                  </span>
                </template>
                <el-slider v-model="form.level" :step="1" :min="-15" :max="15" show-stops />
              </el-form-item>
              <el-row type="flex" justify="center">
                <el-button type="primary" @click="editFace">生 成</el-button>
                <el-button @click="cancel">重 置</el-button>
              </el-row>
            </el-form>
        </el-col>
        <el-col :span="1">
          <el-divider direction="vertical" style="height:100%;margin:auto;" />
        </el-col>
        <el-col :span="11">
          <div class="block">
            <el-image
              v-if="url"
              style="width: 100%;"
              :src="url"
              :fit="cover"
            />
            <el-icon v-else style="font-size: 10rem; height:20rem; width: 100%;">
              <Picture />
            </el-icon>
          </div>
        </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  name: 'App',
  components: {
  },
  data() {
    return {
      webhook: 'ws://172.17.6.195:2345',
      socket: null,
      url: '',
      imageUrl: '',
      loading: false,
      styles: {
        age: '年龄变换',
        eyes_open: '眼睛大小',
        eye_distance: '双眼距离',
        eye_eyebrow_distance: '眉毛距离',
        eye_ratio: '睫毛疏密',
        gender: '性别变换',
        lip_ratio: '脂肪数量',
        mouth_open: '嘴巴闭合',
        nose_mouth_distance: '嘴鼻间距',
        nose_ratio: '鼻梁挺拔',
        nose_tip: '鼻梁倾斜',
        smile: '笑容程度',
        pitch: '喉结大小',
        roll: 'roll',
        yaw: 'yaw',
      },
      form: {
        avatar: '',
        level: 0,
        style: 'age'
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      if (typeof (WebSocket) === "undefined") {
        alert("您的浏览器不支持socket")
      } else {
        this.socket = new WebSocket(this.webhook)
        this.socket.onmessage = this.onMessage
        this.socket.onopen = this.onOpen
        this.socket.onerror = this.onError
        this.socket.onclose = this.onClose
      }
    },
    onMessage (res) {
        let data = JSON.parse(res.data)
        console.log(data)

        this.$message.info(data.msg);

        if(data.code == 200) {
          this.loading = false
          this.url = data.url
        }
    },
    onOpen() {
      console.log("socket连接成功")
    },
    onError(msg) {
      console.error(msg)
    },
    onClose() {
      console.log("socket已经关闭")
    },
    editFace() {
      if(!this.form.avatar) {
        this.$message.error('请先上传头像');
        return false
      }

      this.loading = true;
      this.socket.send(JSON.stringify(this.form))
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      this.form.avatar = res.url
    },
    beforeAvatarUpload(file) {
      if (!['image/jpeg', 'image/gif', 'image/png', 'image/jpg'].includes(file.type)) {
        this.$message.error('上传头像格式错误!');
        return false
      }
      if (file.size / 1024 / 1024 > 10) {
        this.$message.error('上传头像图片大小不能超过 10MB!');
        return false
      }
      return true
    },
    cancel() {
      this.form = {
        avatar: '',
        level: 0,
        style: 'age'
      }
    }
  }
}
</script>

